<template>
  <div class="layout">
    <div class="headerBar spaceBetween">
      <div>
        定时任务配置
      </div>
      <div>
        <i @click="logout" class="ivu-icon ivu-icon-md-log-out icon_logout"></i>
      </div>
    </div>
    <div class="container">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  export default{
    name:"layout",
    data(){
      return{
        
      }
    },
    created() {
      
    },
    methods:{
      logout(){
        this.$router.push("/login")
      }
    }
  }
</script>

<style lang="less">
  .headerBar{
    height: 50px;
    line-height: 50px;
    font-size: 20px;
    font-weight: bold;
    color: white;
    background: linear-gradient(to right,#1E90FF,#4169E1);
    padding: 0 20px;
  }
  .icon_logout{
    font-size: 30px;
    &:hover{
      color: red;
    }
  }
  .container{
    padding: 20px;
  }
  
</style>



